import './App.css';
import Home from './Components/Home'
import Menubar from './Components/Menubar';
import {BrowserRouter as Router, Route,Switch} from 'react-router-dom'
import HospitalBeds from './Components/HospitalBeds';
import Oxygen from './Components/Oxygen'
import Plasma from './Components/Plasma'
import FoodResources from './Components/FoodResources'
import Tweets from './Components/Tweets'
import CovidStats from './Components/CovidStats'
import TelegramGroups from './Components/TelegramGroups'

function App() {




  return (
    <Router>
    <div className="App">
      <div className="container">
      <Menubar />
      <Switch>
      <Route exact path="/HospitalBeds">
          <HospitalBeds/>
        </Route>
        <Route exact path="/Oxygen">
          <Oxygen/>
        </Route>
        <Route exact path="/Plasma">
          <Plasma/>
        </Route>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route exact path="/FoodResources">
          <FoodResources/>
        </Route>
        <Route exact path="/Tweets">
          <Tweets/>
        </Route>
        <Route exact path="/CovidStats">
          <CovidStats/>
        </Route>
        <Route exact path="/TelegramGroups">
          <TelegramGroups/>
        </Route>
        
      </Switch>
      </div>
    </div>
    </Router>
   );
}

export default App;
